import React from 'react';
import { useTranslation } from 'gatsby-plugin-react-i18next';
import images from '../images';

const ServicesTiles = () => {
  const { t } = useTranslation();

  const services = t('services', { returnObjects: true });

  return (
    <div className={styles.wrapper}>
      {Object.keys(services).map((service, index) => {
        const iconKey = t(`services.${service}.icon`);
        const image = images[iconKey];

        return (
          <div key={index} className={styles.tile}>
            {image && (
              <img
                src={image.src}
                alt={image.alt || (t(`services.${service}.title`) as string) || 'Default alt text'}
                className={styles.icon}
              />
            )}
            <h3 className={styles.title}>{t(`services.${service}.title`)}</h3>
            <p>{t(`services.${service}.intro`)}</p>
          </div>
        );
      })}
    </div>
  );
};

export default ServicesTiles;

const styles = {
    wrapper: "grid grid-cols-1 sm:grid-cols-3 gap-4 items-stretch mx-4 max-w-[1400px] mx-auto",
    tile: "text-center p-4 rounded-lg text-white bg-secondary/[0.8] shadow-lg flex flex-col justify-center items-center",
    title: "font-bold text-lg",
    icon: "w-12",
  };
  